.services-section {
    $root: &;
    margin-top: 40px;
    margin-bottom: 40px;

    @media (max-width: 540px) {
        margin-top: 0;
        margin-bottom: 0;
        padding-top: 15px;
        padding-bottom: 15px;
    }

    &_first {
        #{$root} {
            margin-top: 0;
            margin-bottom: 0;
            padding-top: 20px;
            &__container {
                @media (max-width: 768px) {
                    padding-top: 20px;
                }

                @media (max-width: 540px) {
                    padding-top: 10px;
                }
            }

            &__content {
                margin-top: 120px;

                @media (max-width: 1500px) {
                    margin-top: 80px;
                }

                @media (max-width: 1150px) {
                    margin-top: 0;
                }
            }

            &__img {
                margin-left: 100px;

                @media (max-width: 1500px) {
                    margin-left: 0;
                }
            }

            &__title-box {
                @media (max-width: 1150px) {
                    align-items: flex-end;
                }
            }
        }
    }

    &_right {
        #{$root} {
            &__title {
                &:after {
                    left: 0;

                    @media (max-width: 540px) {
                        left: 0;
                    }
                }
            }

            &__container {
                flex-flow: row-reverse;
                align-items: center;
            }

            &__img {
                margin-left: 150px;
                margin-right: 100px;

                @media (max-width: 1500px) {
                    margin-right: 0;
                    margin-left: 60px;
                }
            }

            &__title-box {
                @media (max-width: 1150px) {
                    flex-flow: row-reverse;
                    align-items: center;
                }
            }

            &__img-mobile {
                @media (max-width: 1150px) {
                    margin-right: 0;
                    margin-left: 20px;
                }

                @media (max-width: 540px) {
                    margin-left: 10px;
                }
            }
        }
    }

    &_left {
        #{$root} {
            &__container {
                align-items: center;
            }

            &__img {
                margin-left: 100px;
                margin-right: 150px;

                @media (max-width: 1500px) {
                    margin-right: 60px;
                    margin-left: 0;
                }
            }

            &__title-box {
                @media (max-width: 1150px) {
                    align-items: center;
                }
            }

            &__img-mobile {
                @media (max-width: 1150px) {
                    margin-right: 20px;
                    margin-left: 0;
                }

                @media (max-width: 540px) {
                    margin-right: 10px;
                }
            }
        }
    }

    &_left.services-section_no-img-margin {
        #{$root} {
            &__img {
                margin-left: 0;
            }
        }
    }

    &_right.services-section_no-img-margin {
        #{$root} {
            &__img {
                margin-right: 0;
            }
        }
    }

    &_margin {
        margin-top: 120px;
        margin-bottom: 120px;

        @media (max-width: 1150px) {
            margin-top: 60px;
            margin-bottom: 60px;
        }

        @media (max-width: 540px) {
            margin-top: 30px;
            margin-bottom: 30px;
            padding-top: 0;
            padding-bottom: 0;
        }
    }

    &__title {
        display: inline-block;
        margin-bottom: 0;
        font-size: 52px;
        text-align: left;
        line-height: 1.2em;
        text-transform: uppercase;

        @media (max-width: 1340px) {
            margin-bottom: 0;
        }

        @media (max-width: 850px) {
            font-size: 30px;
        }

        @media (max-width: 540px) {
            margin-bottom: 0;
            font-size: 18px;
        }

        &:after {
            left: 23%;
            width: 80%;

            @media (max-width: 540px) {
                left: 20px;
            }
        }
    }

    &__title-box {
        @media (max-width: 1150px) {
            display: flex;
            align-items: center;
        }

        @media (max-width: 540px) {
            margin-bottom: 15px;
        }
    }

    &__desc {
        line-height: 1.2em;

        @media (max-width: 850px) {
            font-size: 20px;
        }

        @media (max-width: 540px) {
            font-size: 12px;
        }
    }

    &__container {
        display: flex;
        align-items: start;
    }

    &__img {
        max-width: 800px;
        margin-right: 150px;

        @media (max-width: 1500px) {
            margin-right: 60px;
        }

        @media (max-width: 1340px) {
            width: calc(50% - 70px);
            max-width: 100%;
        }

        @media (max-width: 1150px) {
            display: none;
        }

        &-mobile {
            display: none;

            @media (max-width: 1150px) {
                display: block;
                flex: none;
                width: 50%;
                margin-right: 20px;
            }

            @media (max-width: 540px) {
                margin-right: 10px;
            }
        }
    }
}
