.services {
    $root: &;
    padding-top: 30px;

    @media (max-width: 540px) {
        padding-top: 10px;
    }

    &__ico {
        flex: none;
        max-width: 115px;

        @media (max-width: 1200px) {
            width: 80px;
        }

        @media (max-width: 850px) {
            width: 70%;
        }
    }

    &__ico-box {
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        top: 0;
        left: 50%;
        width: 173px;
        height: 173px;
        transform: translate(-50%, -50%);
        background-color: #fff;
        border-radius: 50%;

        @media (max-width: 1200px) {
            width: 120px;
            height: 120px;
        }

        @media (max-width: 850px) {
            width: 70px;
            height: 70px;
        }

        @media (max-width: 540px) {
            width: 40px;
            height: 40px;
        }
    }

    &__item {
        box-sizing: border-box;
        display: flex;
        justify-content: center;
        position: relative;
        width: 420px;
        height: 480px;
        padding-top: 125px;
        background-color: #346929;
        margin-top: 86px;

        @media (max-width: 1400px) {
            width: 32%;
            height: 400px;
        }

        @media (max-width: 1200px) {
            margin-top: 60px;
        }

        @media (max-width: 1050px) {
            height: 290px;
            padding-top: 90px;
        }

        @media (max-width: 850px) {
            margin-top: 35px;
            height: 190px;
            padding-top: 65px;
        }

        @media (max-width: 540px) {
            margin-top: 20px;
            width: calc(33.333% - 11px);
            height: 115px;
            padding: 32px 10px 0 10px;
        }

        @media (max-width: 390px) {
            height: 95px;
        }

        &:last-child {
            #{$root} {
                &__ico {
                    width: 50%;
                }
            }
        }
    }

    &__text {
        color: #fff;
        text-transform: uppercase;
        font-size: 48px;
        line-height: 1.2em;
        text-align: center;

        @media (max-width: 1400px) {
            font-size: 36px;
        }

        @media (max-width: 1050px) {
            font-size: 28px;
        }

        @media (max-width: 850px) {
            font-size: 18px;
        }

        @media (max-width: 540px) {
            font-size: 11px;
        }

        @media (max-width: 390px) {
            font-size: 8px;
        }
    }

    &__list {
        display: flex;
        justify-content: space-between;
    }
}